.scan-btns {
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 40px;
}

.scan-btns button {
  color: #025373;
  height: 50px;
  width: 180px;
  font-size: large;
  align-self: center;
  /* border: 1px solid #025373; */
  border: none;
  font-weight: 600;
  padding: 8px 15px;
  border-radius: 8px;
  cursor: pointer;
  text-transform: uppercase;
}

.scan-btns h3{
  user-select:none;
  color: #023E8A;
}

.btn {
  color: #023E8A;
  text-decoration: none;
}

.pharmacy {
  background-color: #2192bf25;
}

.lab {
  background-color: #ff595f2f;
}


