.main {
  height: 92vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  /* gap: 50px; */
}


.main header {
  user-select: none;
  width: 100%;
  height: 8vh;
  background-color: #CAF0F8;
  display: flex;
  align-items: center;
}

.main header h1 {
  /* display: none; */
  color: #023E8A;
  font-size: large;
  padding: 4px 20px;
}

.container {
  align-self: center;
}

.footer {
  height: 8vh;
  background-color: #CAF0F8;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 2em;
  width: 100vw;
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 15px 5px;
  /* z-index: 1111; */
}

.re-scan {
  width: 30px;
  height: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid #023E8A;
  padding: 10px;
  text-decoration: none;
  border-radius: 50%;
  user-select: none;

}


.re-scan span {
  color: #023E8A;
}

.re-scan span:last-child {
  font-size: small;
}

.next-btn {
  display: flex;
  align-items: center;
  margin-right: 25px;
  padding: 5px 10px;
  /* width: 100px; */
  color: #023E8A;
  border: 1px solid #023E8A;
  border-radius: 5px;
  
}

.next-btn button {
  color: #023E8A;
  background: transparent;
  border: none;
  font-size: large;
  font-weight: 700;
  user-select: none;
}


.path-img img {
  width: 100vw;
  /* height: 65vh; */
}

.ways-title {
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 10px;
  user-select: none;
}

.ways-title h4 {
  color: #023E8A;
  /* height: 2vh; */
  text-align: center;
  /* user-select: none; */
}

@keyframes blink {
  0% {
    opacity: 0;
    /* Fully visible */
    font-size: 40px;
  }

  50% {
    opacity: 1;
    /* Fully invisible */
    font-size: 50px;
  }

  100% {
    opacity: 0;
    font-size: 40px;
    /* Fully visible */
  }
}

@keyframes reach{
  0%{
    font-size: 15px;
    color: #023E8A;
  }
  50%{
    font-size: 20px;
    color: green;
  }
  100%{
    font-size: 15px;
    color: red;
  }
}



.ways-title span {
  color: #023E8A;
  animation: blink 1s infinite;
  /* font-size: 40px; */
  /* user-select: none; */
}

.final-text {
  user-select: none;
  font-size: large;
  color: #023E8A;
  font-weight: 600;
  animation: reach 1s infinite;
}